// app/(root)/allinterviews/page.tsx
import { getDocs, collection, query, orderBy } from 'firebase/firestore';
import { db } from '@/firebase/client';
import FilterableInterviewList from '@/components/FilterableInterviewList';

async function getInterviewsFromDB() {
  const q = query(collection(db, 'interviews'), orderBy('createdAt', 'desc'));
  const snapshot = await getDocs(q);
  return snapshot.docs.map(doc => ({
    id: doc.id,
    ...doc.data(),
  }));
}

export default async function AllInterviewsPage() {
  const interviews = await getInterviewsFromDB();

  return (
    <div className="max-w-6xl mx-auto px-4">
      <h1 className="text-3xl font-bold text-center mb-6">All Interviews</h1>
      <FilterableInterviewList interviews={interviews} />
    </div>
  );
}
